import swal from 'sweetalert2';
import { LoadingComponent } from './../../../loading/loading.component';
import { MessagesComponent } from './../../../messages/messages.component';
import { Pageable } from './../../../../objects/pageable';
import { ProductService } from './../../../../services/product.service';
import { FormControl, Validators } from '@angular/forms';
import { NgbModal, NgbModalRef } from '@ng-bootstrap/ng-bootstrap';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-types',
  templateUrl: './types.component.html',
  styleUrls: ['./types.component.css']
})
export class TypesComponent implements OnInit {

  public pageable: Pageable;
  public titleFormType: string;
  public nameType: FormControl;
  public nameTypeUpdate: FormControl;
  public types: any[];
  private typeData: any;
  private modalRef: NgbModalRef;

  constructor(private ngbModal: NgbModal, private productService: ProductService) { }

  ngOnInit(): void {
    this.types = [];
    this.nameType = new FormControl("", Validators.maxLength(20));
    this.pageable = new Pageable();
    this.findTypes();
  }

  public save(): void {
    LoadingComponent.isActive = true;
    if(this.typeData) {
      this.productService.updateType(this.nameTypeUpdate.value, this.typeData.id).subscribe(
        response => {
          this.ngOnInit();
          this.modalRef.close();
          swal.fire("¡Tipo actualizado!", "", "success");
          LoadingComponent.isActive = false;
        },
        error => {
          MessagesComponent.errorMessage(error);
        }
      );
    } else {
      this.productService.createType(this.nameTypeUpdate.value).subscribe(
        response => {
          this.ngOnInit();
          this.modalRef.close();
          swal.fire("¡Tipo creado!", "", "success");
          LoadingComponent.isActive = false;
        },
        error => {
          MessagesComponent.errorMessage(error);
        }
      );
    }
  }

  public pageChange(page: number): void {
    this.pageable.page = page;
    this.findTypes();
  }

  public openFormType(content: any, typeData: any): void {
    this.titleFormType = typeData ? "Editar tipo" : "Crear tipo";
    this.typeData = typeData;
    this.nameTypeUpdate = new FormControl(typeData ? typeData.name : "", [Validators.required, Validators.maxLength(20)]);
    this.modalRef = this.ngbModal.open(content, {size: "lg"});
  }

  public findTypes(): void {
    LoadingComponent.isActive = true;
    this.productService.findTypes(this.nameType.value === "" ? "all" : this.nameType.value, this.pageable.page, this.pageable.size).subscribe(
      response => {
        if(this.nameType.value != "") {
          this.pageable.page = 1;
        }
        this.pageable.uploadPageable(response);
        this.types = response.content;
        LoadingComponent.isActive = false;
      },
      error => {
        MessagesComponent.errorMessage(error);
      }
    );
  }

}
