interface genericData {
    id: number,
    name: string
}
export class Product {

    public images: number[];
    public code: string;
    public name: string;
    public description: string;
    public colours: genericData[];
    public units: number;
    public unitType: string;
    public brands: genericData[];
    public typeId: number;

    constructor(productForm: any, colours: genericData[], brands: genericData[]) {
        this.images = [1,2];
        this.code = productForm.code;
        this.name = productForm.name;
        this.description = productForm.description;
        this.colours = colours;
        this.units = productForm.units;
        this.unitType = productForm.unitType;
        this.brands = brands;
        this.typeId = productForm.typeId;
    }

}
