import { Pageable } from './../../../../objects/pageable';
import { LoadingComponent } from './../../../loading/loading.component';
import { MessagesComponent } from './../../../messages/messages.component';
import { FormControl, Validators } from '@angular/forms';
import { ProductService } from './../../../../services/product.service';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-products',
  templateUrl: './products.component.html',
  styleUrls: ['./products.component.css']
})
export class ProductsComponent implements OnInit {

  public pageable: Pageable;
  public products: any[];
  public name: FormControl;
  public isCleanSearch: boolean;

  constructor(private productService: ProductService) { }

  ngOnInit(): void {
    this.products = [];
    this.isCleanSearch = false;
    this.pageable = new Pageable();
    this.name = new FormControl("", Validators.maxLength(100));
    this.findProduct("all");
  }

  public pageChange(page: number): void {
    this.pageable.page = page;
    this.findProduct("all");
  }

  public search(): void {
    let name: string = this.name.value != "" ? this.name.value : "all";
    this.isCleanSearch = this.name.value != "";
    this.findProduct(name);
  }

  public findProduct(name: string): void {
    LoadingComponent.isActive = true;
    this.productService.find(name, this.pageable.page, this.pageable.size).subscribe(
      response => {
        if(name != "all") {
          this.pageable.page = 1;
        }
        this.pageable.uploadPageable(response);
        this.products = response.content;
        LoadingComponent.isActive = false;
      },
      error => {
        MessagesComponent.errorMessage(error);
      }
    );
  }

}
