import { ActivatedRoute, Router } from '@angular/router';
import { LoadingComponent } from './../../../loading/loading.component';
import { MessagesComponent } from './../../../messages/messages.component';
import { ProductService } from './../../../../services/product.service';
import { Component, OnInit, ViewChild } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import swal from 'sweetalert2';
import { Product } from 'src/app/objects/product';
import { NgbCarousel, NgbSlideEvent, NgbSlideEventSource } from '@ng-bootstrap/ng-bootstrap';

enum GenericDataType {
  COLOUR,
  BRAND
}

interface GenericData {
  id: number,
  name: string,
  isCreate: boolean
}

@Component({
  selector: 'app-form-product',
  templateUrl: './form-product.component.html',
  styleUrls: ['./form-product.component.css']
})
export class FormProductComponent implements OnInit {

  public title: string;
  public formProduct: FormGroup;
  public brands: GenericData[];
  public colours: GenericData[];
  public imagesUrls: string[];
  public types: any[];
  public typeForm: string;
  private productId: number;
  public images: File[];
  public paused = false;
  public unpauseOnArrow = false;
  public pauseOnIndicator = false;
  public pauseOnHover = true;
  public pauseOnFocus = true;
  @ViewChild('carousel', { static: true })
  public carousel: NgbCarousel;

  constructor(private productService: ProductService, private router: Router, private activatedRoute: ActivatedRoute) { }

  ngOnInit(): void {
    this.imagesUrls = [];
    this.images = [];
    this.getTypes();
    this.colours = [];
    this.brands = [];
    this.typeForm = this.activatedRoute.snapshot.paramMap.get("type");
    if (this.typeForm === "create") {
      this.title = "Crear producto";
      this.formProduct = new FormGroup({
        name: new FormControl("", [Validators.required, Validators.maxLength(50)]),
        description: new FormControl("", [Validators.required, Validators.maxLength(200)]),
        units: new FormControl(null, [Validators.required, Validators.max(999999999)]),
        unitType: new FormControl("", [Validators.required, Validators.maxLength(20)]),
        code: new FormControl("", Validators.required),
        colour: new FormControl(""),
        brand: new FormControl("", Validators.maxLength(50)),
        typeId: new FormControl(null, Validators.required)
      });
    } else if (this.typeForm === "update") {
      let productId: number = Number.parseInt(this.activatedRoute.snapshot.queryParamMap.get("productId"));
      if (productId) {
        this.title = "Editar producto";
        LoadingComponent.isActive = true;
        this.productService.findById(productId).subscribe(
          response => {
            this.productId = response.product.id;
            this.imagesUrls = response.images;
            for (let colour of response.colours) {
              this.colours.push({ id: colour.id, name: colour.name, isCreate: false });
            }
            for (let brand of response.brands) {
              this.brands.push({ id: brand.id, name: brand.name, isCreate: false });
            }
            this.formProduct = new FormGroup({
              images: new FormControl(),
              name: new FormControl(response.product.name, [Validators.required, Validators.maxLength(50)]),
              description: new FormControl(response.product.description, [Validators.required, Validators.maxLength(200)]),
              units: new FormControl(response.product.units, [Validators.required, Validators.max(999999999)]),
              unitType: new FormControl(response.product.unitType, [Validators.required, Validators.maxLength(20)]),
              code: new FormControl(response.product.code, Validators.required),
              colour: new FormControl(""),
              brand: new FormControl("", Validators.maxLength(50)),
              typeId: new FormControl(response.productType.id, Validators.required)
            });
            LoadingComponent.isActive = false;
          },
          error => {
            MessagesComponent.errorMessage(error);
          }
        );
      }
    }
  }

  public togglePaused(): void {
    if (this.paused) {
      this.carousel.cycle();
    } else {
      this.carousel.pause();
    }
    this.paused = !this.paused;
  }

  public onSlide(slideEvent: NgbSlideEvent): void {
    if (this.unpauseOnArrow && slideEvent.paused &&
      (slideEvent.source === NgbSlideEventSource.ARROW_LEFT || slideEvent.source === NgbSlideEventSource.ARROW_RIGHT)) {
      this.togglePaused();
    }
    if (this.pauseOnIndicator && !slideEvent.paused && slideEvent.source === NgbSlideEventSource.INDICATOR) {
      this.togglePaused();
    }
  }

  public selectImages(event): void {
    if (event.target.files.length > 0) {
      this.images = event.target.files;
    }
  }

  private getTypes(): void {
    LoadingComponent.isActive = true;
    this.productService.getAllTypes().subscribe(
      response => {
        this.types = response;
        LoadingComponent.isActive = false;
      },
      error => {
        MessagesComponent.errorMessage(error);
      }
    );
  }

  public create(): void {
    LoadingComponent.isActive = true;
    if (this.typeForm === "create") {
      this.productService.create(new Product(this.formProduct.getRawValue(), this.colours, this.brands)).subscribe(
        response => {
          let formData: FormData = new FormData();
          for (let i: number = 0; i < this.images.length; i++) {
            formData.append("files", this.images[i]);
          }
          this.productService.uploadImagesByProduct(formData, response.productId).subscribe(
            response => {
              this.ngOnInit();
              swal.fire("¡Producto guardado!", "", "success");
              this.router.navigate(["/admin/products"]);
              LoadingComponent.isActive = false;
            },
            error => {
              MessagesComponent.errorMessage(error);
            }
          );
        },
        error => {
          MessagesComponent.errorMessage(error);
        }
      );
    } else if (this.typeForm === "update" && this.productId) {
      this.productService.update(new Product(this.formProduct.getRawValue(), this.colours, this.brands), this.productId).subscribe(
        response => {
          if (this.images.length > 0) {
            let formData: FormData = new FormData();
            for (let i: number = 0; i < this.images.length; i++) {
              formData.append("files", this.images[i]);
            }
            this.productService.uploadImagesByProduct(formData, this.productId).subscribe(
              response => {
                this.ngOnInit();
                swal.fire("¡Producto actualizado!", "", "success");
                this.router.navigate(["/admin/products"]);
                LoadingComponent.isActive = false;
              },
              error => {
                MessagesComponent.errorMessage(error);
              }
            );
          } else {
            this.ngOnInit();
            swal.fire("¡Producto actualizado!", "", "success");
            this.router.navigate(["/admin/products"]);
            LoadingComponent.isActive = false;
          }
        },
        error => {
          MessagesComponent.errorMessage(error);
        }
      );
    }
  }

  private deleteGenericData(genericData: GenericData, type: GenericDataType): void {
    LoadingComponent.isActive = true;
    this.productService.deleteGenericData(genericData.id).subscribe(response => {
      swal.fire("¡Eliminado!", "", "success");
      if (type === GenericDataType.BRAND) {
        for (let i: number = 0; i < this.brands.length; i++) {
          if (this.brands[i].name === genericData.name) {
            this.brands.splice(i, 1);
          }
        }
      } else if (type === GenericDataType.COLOUR) {
        for (let i: number = 0; i < this.colours.length; i++) {
          if (this.colours[i].name === genericData.name) {
            this.colours.splice(i, 1);
          }
        }
      }
      LoadingComponent.isActive = false;
    },
      error => {
        MessagesComponent.errorMessage(error);
      });
  }

  public deleteColour(colour: GenericData): void {
    if (this.typeForm === "update" && colour.id != null) {
      swal.fire({
        title: "¿Estas seguro de eliminar este color?",
        icon: "warning",
        showCancelButton: true,
        showConfirmButton: true,
        confirmButtonText: "Si!",
        cancelButtonText: "No!"
      }).then(
        result => {
          if (result.isConfirmed) {
            this.deleteGenericData(colour, GenericDataType.COLOUR);
          }
        }
      );
    } else if (this.typeForm === "create" || colour.isCreate) {
      for (let i: number = 0; i < this.colours.length; i++) {
        if (this.colours[i].name === colour.name) {
          this.colours.splice(i, 1);
        }
      }
    }
  }

  public addColour(): void {
    if (this.formProduct.get("colour").value != "") {
      let result: GenericData[] = this.colours.filter(b => b.name === this.formProduct.get("colour").value);
      if (result.length === 0) {
        this.colours.push({ id: null, name: this.formProduct.get("colour").value, isCreate: true });
        this.formProduct.get("colour").setValue(null);
      } else {
        swal.fire("¡Ya seleccionaste este color!", "", "info");
      }
    }
  }

  public addBrand(): void {
    if (this.formProduct.get("brand").value != "") {
      let result: GenericData[] = this.brands.filter(b => b.name === this.formProduct.get("brand").value);
      if (result.length === 0) {
        this.brands.push({ id: null, name: this.formProduct.get("brand").value, isCreate: true });
        this.formProduct.get("brand").setValue("");
      } else {
        swal.fire("¡Esta marca ya esta agregada!", "", "info");
      }
    }
  }

  public deleteBrand(brand: GenericData): void {
    if (this.typeForm === "update" && brand.id != null) {
      swal.fire({
        title: "¿Estas seguro de eliminar esta marca?",
        icon: "warning",
        showCancelButton: true,
        showConfirmButton: true,
        confirmButtonText: "Si!",
        cancelButtonText: "No!"
      }).then(
        result => {
          if (result.isConfirmed) {
            this.deleteGenericData(brand, GenericDataType.BRAND);
          }
        }
      );
    } else if (this.typeForm === "create" || brand.isCreate) {
      for (let i: number = 0; i < this.brands.length; i++) {
        if (this.brands[i].name === brand.name) {
          this.brands.splice(i, 1);
        }
      }
    }
  }

}
